.movie-box{
    width:6.92rem;
    margin:0 auto;
    margin-bottom:1rem;
    .item-dl{
        width:100%;
        height:2.26rem;
        display:flex;
        dt{
            width:1.48rem;
            height:100%;
            img{
                display:block;
                width:1.28rem;
                height:1.8rem;
                margin-top:0.24rem;
            }
        }
        dd{
            width:5.44rem;
            height:100%;
            ul{
                width:100%;
                height:100%;
                padding:0.28rem 0 0 0;
                box-sizing:border-box;
                display:flex;
                flex-direction:column;
                .title-li{
                    width:100%;
                    font-size:0.3rem;
                    font-weight:bold;
                    color:#333333;
                    margin-bottom:0.26rem;
                }
                .rate-li{
                    width:100%;
                    font-size:0.22rem;
                    color:#666666;
                    span{
                        font-size:0.2rem;
                        color:#eeb23d;
                    }
                    margin-bottom:0.18rem;
                }
                .name-li{
                    width:100%;
                    white-space:nowrap;
                    overflow:hidden;
                    text-overflow:ellipsis;
                    font-size:0.22rem;
                    color:#666666;
                    margin-bottom:0.18rem;
                }
                .time-li{
                    width:100%;
                    font-size:0.22rem;
                    color:#666666;
                }
            }
        }
    }
}